.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.context {
  display: flex;
}

@media all and (min-width: 1060px) { 
.bigbox {
  
  display: flex;
  text-align: left;
  padding: 50px;
  width: 100%;
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: 150px;
  margin-right: 150px;
  border-radius: 20px;
  background: #f8f8f8;
}

.left {
  display: inline-block;
  width: 50%;
}

.bigbox .chart {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 50%;
}
}

@media (min-width: 640px) and (max-width: 1060px) { 

  .bigbox {
  
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    padding: 50px;
    width: 100%;
    margin-top: 15px;
    margin-bottom: 15px;
    margin-left: 50px;
    margin-right: 50px;
    border-radius: 20px;
    background: #f8f8f8;
  }

  .left {
    display: inline-block;
    width: 100%;
  }

  .bigbox .chart {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
  }

  .ops-select {
    width: 100%;
  }

  .ops-list {
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: space-around;
    width: 100%;
    height: 80px;
    padding: 0;
  }

  .opsgraph {
    width: 100%;
    height: 300px;
    margin-bottom: 80px;
  }

  .ops-olt {
    width: 100%;
  }
}


.bigbox #des {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  padding-left: 4px;
  color: #A576DE;
}

.bigbox #bignum {
  display: inline-block;
  font-size: 72px;
  font-weight: 900;
  line-height: 100%;
}

.bigbox #cus {
  display: inline-block;
  padding-left: 10px;
  font-size: 24px;
  font-weight: 900;
  line-height: 100%;
}

.emptybox {
  padding: 100px;
}



.ratiodes {
  text-align: left;
  font-size: 24px;
  font-weight: 700;
  color: #565a64;
  display: inline-block;
  padding-left: 4px;
  height: 20%;
}

.bar {
  display: inline-block;
  box-sizing: border-box;
	background: #fff;
  width: 100%;
  height: 15%;
	border-radius: 8px;
  overflow: hidden;
  margin-top: 20px;
  margin-bottom: 20px;
}

.level {
  display: inline-block;
  width: 100%;
	height: 100%;
  padding-left: 1px;
}

#list {
  display: flex;
  flex-direction: column;
  list-style: none;
  justify-content: space-around;
  width : 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#list li {
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
  margin-bottom: 10px;
}

#list .colorbox {
  text-align: left;
  width: 10%;
  height: 100%;
  margin-right: 20px;
  border-radius: 5px;
}

#list .icon {
  font-size: 20px;
  text-align: left; 
  width: 30%;
}

#list .num {
  font-size: 20px;
  font-weight: 700;
  text-align: left;
  width: 40%;
}

#list .ratio {
  text-align: right; 
  font-size: 20px;
  font-weight: 700;
  color: #565a64;
  width: 20%;
}

/* 여기까지 서울/강남 표현 */

.select{
  display: flex;
}

.des-ops {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  padding-left: 4px;
  color: #A576DE;
}

#selectbox {
  display: flex;
  flex-direction: column;
}

.ops-list {
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: space-around;
  height: 80px;
  padding: 0;
}

.ops-list button {
  width: 80%;
  height: 100%;
  padding: 0;
  margin: 10px;
  color: #3B614C;
  font-weight: 700;
  background-color: #fff;
  border-color: #3B614C;
  border-radius: 10px;
  box-shadow: 3px 3px 3px #565a64;
}

.ops-list button:hover {
  background: #3B614C;
  color: #fff;
  transition: all .05s ease-in-out .05s;
}

.ops-list button:focus {
  background: #3B614C;
  color: #fff;
  box-shadow: none;
}

/* 여기까지 운용팀별 클릭 버튼 구현 */

.opsgraph {
  width: 50%;
  height: 300px;
  margin-bottom: 30px;
}

.opsgraph p {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  padding-left: 4px;
  color: #A576DE;
}

.ops-olt {
  width: 50%;
}

.ops-olt p {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  padding-left: 4px;
  color: #565a64;
}

/* 여기까지 상세보기 div  */

.search {
  display: flex;
}

.searchbox {
  display: flex;
  width:100%;
  align-items: center;
  flex-direction: column;
  margin-top:50px;
  margin-bottom:50px;
}

.searchbox p {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #565a64;
}

.searchbar {
  display: flex;
}

.searchbar input{
  border-radius: 5px;
  box-shadow: 3px 3px 3px #565a64;
}

.searchbar button{
  border: none;
  border-radius: 5px;
  background: #f8f8f8;
  margin-left: 5px;
  box-shadow: 3px 3px 3px #565a64;
}

.searchbar button:hover{
  box-shadow: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
